@import "./variables.scss";
.navbar {
  margin: 0px;
}

#nav-head {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  font-family: "Kalam";
  z-index: 1;
}

.navbar img {
  height: 60px;
  width: 60px;
  border-radius: 50%;
}

.nav-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-title-top,
.nav-title-bottom {
  font-size: 1rem;
  line-height: 70%;
  font-weight: 1000;
}

.nav-title-top {
  color: $prime;
}

.nav-title-bottom {
  color: $sec;
}
