.hero {
  background-size: cover;
  background-repeat: no-repeat;
  height: 70vh;
}

.hero-inner {
  text-align: center;
  color: white;
  padding: 4rem;
  border: 2px solid white;
}

.trans-layer {
  background-color: rgba(0, 0, 0, 0.5);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
